.contacts-header {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 1rem;
}

.forms {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
}

.forms input {
  margin: 0 1rem;
  padding: 0.5rem;
  font-size: 1.2rem;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.forms button {
  margin: 0 1rem;
  padding: 0.5rem 1rem;
  font-size: 1.2rem;
  border-radius: 5px;
  border: none;
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
}

.forms button:hover {
  background-color: #0069d9;
}

.afficher {
  background-color: #ffc107;
  color: #212529;
  margin-bottom: 1rem;
}

.afficher:hover {
  background-color: #e0a800;
}

.rech {
  padding: 0.5rem;
  font-size: 1.2rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin-bottom: 1rem;
}

.contacts-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.contacts-list li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  border-bottom: 1px solid #ccc;
}

.contacts-list li .contact-info {
  display: flex;
  align-items: center;
}

.contacts-list li .contact-info h4 {
  margin-right: 1rem;
}

.contacts-list li .contact-info p {
  margin: 0;
}

.contacts-list li .contact-actions {
  display: flex;
  align-items: center;
}

.contacts-list li .contact-actions button {
  margin-left: 1rem;
  font-size: 1.2rem;
  padding: 0.5rem;
  border-radius: 5px;
  border: none;
  background-color: #dc3545;
  color: #fff;
  cursor: pointer;
}

.contacts-list li .contact-actions button:hover {
  background-color: #c82333;
}
